<template>
  <div class="timely">
    <input type="text" class="change-input" placeholder="请输入你想说的话，例如: 你好" v-model="words">
    <a href="javascript:;" class="btn-play" @click="handleSpeak">说</a>
  </div>
</template>
<script>
import wordApi from '@/api/word'
export default {
  data () {
    return {
      words: ''
    }
  },
  methods: {
    handleSpeak () {
      wordApi.change(this.words).then(res => {
        console.dir(res)
      }, err => {
        console.dir(err)
      })
    }
  }
}
</script>
<style scoped>
  .change-input {
    display: block;
    width: 80%;
    margin: 35% auto 20%;
  }
  .btn-play {
    display: block;
    width: 1.4rem;
    height: 1.4rem;
    margin: 0 auto;
    line-height: 1.4rem;
    text-align: center;
    font-size: .7rem;
    color: #fff;
    text-decoration: none;
    border-radius: 50%;
    background-color: #efff49;
  }
</style>



